<template>
	<el-card class="welcome-container" v-loading="loading">
		<!--<h1></h1>-->
		<!---->
		<!--<div slot="header" class="btns">-->
		<!--<el-button type="primary" @click="addNewPage">-->
		<!--</el-button>-->
		<!--</div>-->
		<!---->
		<el-form inline>
			<el-form-item label="ID">
				<el-input @keyup.enter.native="search" v-model="searchOptions.id" placeholder="id" clearable></el-input>
			</el-form-item>
			<el-form-item label="">
				<el-input @keyup.enter.native="search" size="small" v-model="searchOptions.title" placeholder="" clearable></el-input>
			</el-form-item>
			<el-form-item label="">
				<el-select @change="search" v-model="searchOptions.status" placeholder="" clearable>
					<el-option v-for="(item, index) in statusOptions" :key="index" :label="item.label" :value="item.value"></el-option>
				</el-select>
			</el-form-item>
			<el-form-item>
				<template>
					<el-button @click="search"></el-button>
				</template>
			</el-form-item>
			<el-form-item class="float-right">
				<template>
					<el-button type="primary" @click="addNewPage">
					</el-button>
				</template>
			</el-form-item>
		</el-form>

		<!---->
		<div class="list-view">
			<el-table :data="pages" border style="width: 100%" key="force-index" :height="innerHeight">
				<el-table-column prop="id" label="ID" width="100"></el-table-column>
				<el-table-column prop="title" label=""></el-table-column>
				<el-table-column prop="status" label="" width="120" align="center">
					<template slot-scope="scope">
						{{getStatusLabel(scope.row.status, scope.row.admin_name)}}
					</template>
				</el-table-column>
				<el-table-column prop="show_start_time" label="" align="center">
					<template slot-scope="scope">
						{{parsedTime([scope.row.show_start_time, scope.row.show_end_time])}}
					</template>
				</el-table-column>
				<el-table-column label="" fixed="right" width="350" align="center">

					<el-button-group slot-scope="scope">
						<el-button @click="handleEdit(scope.row)">
						</el-button>
						<el-button :disabled="!scope.row.web_url" @click="handlePreview(scope.row.web_url,scope.row.title)">
						</el-button>
						<el-button @click="handleCopy(scope.row.id)">
						</el-button>
						<el-button @click="handleAnalyse(scope.row.web_url)">
						</el-button>
						<el-button @click="handleDelete(scope.row.id)">
						</el-button>
					</el-button-group>

				</el-table-column>
			</el-table>
		</div>

		<!---->
		<div class="pagination-view">
			<el-pagination background layout="prev, pager, next" :current-page.sync="currentPage" :page-size="limit" :total="pagesCount" @current-change="changePagination"></el-pagination>
		</div>

	</el-card>
</template>
<script>
	import { format } from 'date-fns'

	export default {
		name: 'welcome',
		data () {
			return {
				innerHeight: Math.max([innerHeight - 200, 600]),
				loading: true,
				limit: 12,
				currentPage: 1,
				pages: [],
				pagesCount: 0,
				blank: window.parent !== window,
				searchOptions: {
					id: '',
					title: '',
					status: ''
				},
				statusOptions: [
					{
						value: 2,
						label: ''
					}, {
						value: 1,
						label: ''
					}, {
						value: 0,
						label: ''
					}],
				tableData: [{
					data_id: '123456',
					data_title: 'H5',
					data_type: '',
					data_time: '2018-01-23'
				}]
			}
		},
		methods: {

			search () {
				this.currentPage = 1
				this.getPages(this.searchOptions)
			},
			changePagination () {
				this.getPages(this.searchOptions)
			},
			async getPages (options = {}) {
				this.loading = true
				await this.$api.getPageList({
					id: options.id || '',
					title: options.title || '',
					status: options.status === 0 ? 0 : (options.status || ''),
					offset: this.limit * (this.currentPage - 1),
					limit: this.limit

				}).then(res => {
					this.pages = res.list || []
					this.pagesCount = res.total
				}).catch(e => {
					console.log(e)
					this.pages = []
				})
				this.loading = false
			},
			getStatusLabel (status = 0, admin) {
				if (admin) return `${admin}`
				return this.statusOptions.find(option => option.value === status).label
			},
			parsedTime (timestamp) {
				if (Array.isArray(timestamp)) {
					if (timestamp[0] === 0 && timestamp[1] === 0) {
						return ''
					}
					return timestamp.map(ts => this.parsedTime(ts)).join(' - ')
				} else if (timestamp === 0) {
					return ''
				}
				return format((Number(timestamp) * 1000), 'YYYY-MM-DD HH:mm:ss')
			},
			addNewPage () {
//				this.$router.push({name:'add-page'})
				this.blank ? window.open('#/add') : this.$router.push({name: 'add-page'})
			},
			handleEdit (row) {

				this.blank ? window.open('#/edit/' + row.id) : this.$router.push({
					name: 'edit-page',
					params: {
						id: row.id
					}
				})
			},
			handlePreview (url, title) {
				if (!url || !title) {
					this.$message.warning('')
				}
				this.$pagePreviewer.preview(url, title)
				// window.open(url, '', 'width=375,height=603')
			},
			handleCopy (id) {
				this.blank ? window.open('#/copy/' + id)
					: this.$router.push({
						name: 'copy-page',
						params: {
							id
						}
					})
			},
			handleAnalyse (url) {
				if (!url) {
					return this.$message.warning('')
				}
				console.log(url)
				window.open(`/admin.php?m=admin&c=WebTracking&a=index&url=${encodeURIComponent(url)}&app_name=force`)
			},
			handleDelete (id) {
				this.$confirm(', ?', '', {
					confirmButtonText: '',
					cancelButtonText: '',
					type: 'warning'
				}).then(() => {
					this.$api.deletePage(id).then(response => {
						if (response.error_code === 0) {
							this.$message.success('!')
							this.getPages()
						} else {
							this.$message.warning(response.error_message || response.error_description)
						}
					}).catch(err => {
						this.$message.error(err.message)
					})
				}).catch((e) => {

				})
			}
		},
		created () {
			this.getPages()
			console.log(window.parent === window)
		}
	}
</script>
<style lang="less" scoped>
	.welcome-container {
		padding-top: 20px;
		min-width: 1024px;
	}

	.btns {
		margin: 10px 0;
		display: flex;
		justify-content: flex-end;
	}

	.filtrate-view {
		display: flex;
		align-items: center;
		align-content: center;
		justify-content: center;
		li {
			color: teal;
			display: flex;
			align-items: center;
			margin-right: 10px;
			label {
				white-space: nowrap;
				margin-right: 5px;
			}
			button {
				background-color: teal;
			}
		}
	}

	.list-view {
	}

	.pagination-view {
		text-align: center;
		margin: 15px auto 10px;
	}

	.float-right {
		float: right;
		&:after{
			clear: both;
		}
	}

</style>



